.meanings{
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 55vh;
    border:10px solid rgb(105,105,105);
    border-radius: 10px;
    padding: 10px 20px;
    overflow-x: hidden;
}

.subTitle{
    font-size: 3vw;
    font-family: "Montserrat",sans-serif;
    

}
.singleMean{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin:10px 0;
}
@media(max-width:900px){
    .meanings{
        height: 80vh;
        overflow-x: hidden;
        overflow-y: scroll;

    }
}